import React, { useContext } from "react";
import ToDoContext from "../ToDoContet";

export const ToDoView = () => {
  const { toDos, removeTodo } = useContext(ToDoContext);
  return (
    <div>
      {toDos.map((item, i) => {
        return (
          <div onClick={() => removeTodo(i)}>
            {item}
           
          </div>
        );
      })}
    </div>
  );
};
